*{
  padding:0;
  margin:0;
  box-sizing:border-box;
  scroll-behavior: smooth;
}
.contain{
  width: 100vw;
  height:100vh;
  display:grid;
  place-items:center;
  position:relative;
}

.containTabs{
  width:80%;
  height: 80%;
  box-shadow:0px 0px 22px black;
  position:relative;
  scroll-snap-align: start;
  scroll-snap-type:both mandatory;
}

.buttonComponent{
  width:20%;
  height:100%;
  position:absolute;
  
 display:flex;flex-direction:column;
 justify-content: space-around;
 place-items:center;
 background:rgb(37, 35, 35);
}
.buttonComponent >a >  button{
  width:25vmin;
  height:13vmin;background:rgb(77, 77, 77);
 border:none;
 
 font-size:3vmin;
 transition:all 100ms linear;
 font-weight:bold;
 

 color:white;
}
.buttonComponent >a > button:hover{
 
  color:rgb(0, 143, 119);
  background:white;
}

.infoComponent{
  width:80%;
  height:100%;
  
  position:absolute;
  right:0%;
  overflow-y: scroll;
  overflow-x:hidden ;
  scroll-snap-align: start;
  scroll-snap-type:both mandatory;
}
.Card{
  width: 100%;
  height:100%;
  scroll-snap-align: start;
  scroll-snap-type:both mandatory;
  position:relative;
  padding:3vmin;
}
.Card:nth-child(1){
  background:royalblue;
  color:white;
}
.Card:nth-child(2){
  background:tomato;
  color:white
}
.Card:nth-child(3){
  background:rgb(255, 174, 0);
  color:black;
}

.title{
  font-size:5vmin;
  text-align:center;
}
.company{
  background:white;
  color:black;
  font-weight: bold;
  width:15vmin;
  height:5vmin;
  display:grid;
  place-items:center;
  border-radius:2vmin;
  position: absolute;
  top:15%;
}
.date{
  font-weight: bold;
  font-size: 3vmin;
  position: absolute;
  top:25%;
  text-decoration:underline;
}
.duties{

  width: 100%;
  height:50%;
  bottom:5%;
  position: absolute;
  padding:3vmin;
  
  list-style-type: square;
}
.duties>li{
  font-size: 2vmin;
}

@media only screen and (max-width:550px){
  .buttonComponent{
    padding:2vmin;
    overflow: hidden;
  }
  .buttonComponent >a >  button{
    width:15vmin;
    height:7vmin;background:rgb(77, 77, 77);
   border:none;
   
   font-size:3vmin;
   transition:all 100ms linear;
   font-weight:bold;
   
  
   color:white;
  }
  .company{
    width:25vmin;
    height: 9vmin;
  }
  .date{
    font-size:4vmin;
  }
  .duties{
    left:3%;
    padding: 3vmin;
    overflow-y: scroll;
    overflow-x: hidden;
    width:90%
  }
  .duties > li{
    font-size:3vmin;
    white-space: pre-wrap;
    line-height: 4.5vmin;
  }
}